import React from "react";
import UpgradeButton from "./UpgradeButton";

const CreateButtons = ({ upgradeButtons, state }) => {
  const buttons = upgradeButtons.map((button, index) => (
    <UpgradeButton
      key={index}
      state={state}
      cookiesAmount={state.cookiesAmount}
      text={button.text}
      lvl={button.lvl}
      cost={button.cost}
      click={button.click}
    />
  ));

  return <>{buttons}</>;
};

export default CreateButtons;
