import React from "react";

const UpgradeButton = ({ click, lvl, cost, cookiesAmount, text }) => {
  const doesItAvailableToBuy =
    cookiesAmount >= cost ? "available" : "unavailable";

  return (
    <div data-option="test" onClick={click} className={doesItAvailableToBuy}>
      <strong>{lvl}.LVL</strong> | {text} (Koszt: {cost} ciasteczek)
    </div>
  );
};

export default UpgradeButton;
