import React, { Component } from "react";
import "./App.css";
import Cookie from "./comp/Cookie";
import Cookies from "js-cookie";
import CreateButtons from "./comp/CreateButtons";

class App extends Component {
  state = {
    cookiesAmount: 0,
    multiplier: 1,
    multiplierLevel: 1,
    upgradeMultiplerPrice: 200,
    idleCookiesPerSecond: 1,
    idleCookiesPerSecondCost: 500,
    idleCookiesPerSecondLevel: 1,
  };

  gameSettings = {
    multiplier: 1,
    upgradeMultiplerPrice: 200,
  };

  componentDidMount() {
    this.idleCookies();
    this.createCookies();
    this.setStateFromCookies();
  }

  createCookies = () => {
    if (!Cookies.get("cookiesAmount")) {
      Cookies.set("cookiesAmount", 0);
      Cookies.set("multiplier", this.gameSettings.multiplier);
      Cookies.set("upgradeMultiplerPrice", 200);
      Cookies.set("multiplierLevel", 1);
      Cookies.set("idleCookiesPerSecond", 1);
      Cookies.set("idleCookiesPerSecondCost", 500);
      Cookies.set("idleCookiesPerSecondLevel", 1);
    }
  };

  setStateFromCookies = () => {
    this.setState({
      multiplier: Cookies.get("multiplier") * 1,
      multiplierLevel: Cookies.get("multiplierLevel") * 1,
      cookiesAmount: Cookies.get("cookiesAmount") * 1,
      upgradeMultiplerPrice: Cookies.get("upgradeMultiplerPrice"),
      idleCookiesPerSecond: Cookies.get("idleCookiesPerSecond") * 1,
      idleCookiesPerSecondCost: Cookies.get("idleCookiesPerSecondCost") * 1,
      idleCookiesPerSecondLevel: Cookies.get("idleCookiesPerSecondLevel") * 1,
    });
  };

  clickOnCookie = (e) => {
    this.setState({
      cookiesAmount: this.state.cookiesAmount + this.state.multiplier * 1,
    });
    Cookies.set("cookiesAmount", this.state.cookiesAmount);
  };

  UpgradeMultiplier = () => {
    if (this.state.cookiesAmount >= this.state.upgradeMultiplerPrice) {
      this.setState({
        multiplier: this.state.multiplier * 2,
        multiplierLevel: this.state.multiplierLevel + 1,
        cookiesAmount:
          this.state.cookiesAmount - this.state.upgradeMultiplerPrice,
        upgradeMultiplerPrice: Math.round(this.state.upgradeMultiplerPrice * 2),
      });
      Cookies.set("multiplier", this.state.multiplier * 2);
      Cookies.set("multiplierLevel", this.state.multiplierLevel + 1);

      Cookies.set(
        "upgradeMultiplerPrice",
        Math.round(Cookies.get("upgradeMultiplerPrice") * 2)
      );
    } else {
      alert(
        `Brakuje ci ${
          this.state.upgradeMultiplerPrice - this.state.cookiesAmount
        } ciasteczek`
      );
    }
  };

  UpgradeIdleCookiesPerSecond = (e) => {
    if (this.state.cookiesAmount >= this.state.idleCookiesPerSecondCost) {
      this.setState({
        idleCookiesPerSecond: this.state.idleCookiesPerSecond * 2,
        cookiesAmount:
          this.state.cookiesAmount - this.state.idleCookiesPerSecondCost,
        idleCookiesPerSecondCost: this.state.idleCookiesPerSecond * 2 * 500,
        idleCookiesPerSecondLevel: this.state.idleCookiesPerSecondLevel + 1,
      });
      Cookies.set(
        "idleCookiesPerSecond",
        Cookies.get("idleCookiesPerSecond") * 2
      );
      Cookies.set(
        "idleCookiesPerSecondCost",
        Cookies.get("idleCookiesPerSecond") * 500
      );
      Cookies.set(
        "idleCookiesPerSecondLevel",
        this.state.idleCookiesPerSecondLevel + 1
      );
    } else {
      alert(
        `Brakuje ci ${
          this.state.idleCookiesPerSecondCost - this.state.cookiesAmount
        } ciasteczek`
      );
    }
  };

  upgradeFuncion = (e) => {
    if (this.state.cookiesAmount >= this.state[e.target]) {
    }
  };

  idleCookies = () => {
    setInterval(() => {
      this.setState({
        cookiesAmount:
          this.state.cookiesAmount + this.state.idleCookiesPerSecond,
      });
      Cookies.set("cookiesAmount", this.state.cookiesAmount);
    }, 1000);
  };

  gameRestart = () => {
    const decison = window.confirm("Czy na pewno chcesz rozpocząć od nowa?");
    if (decison) {
      Cookies.set("cookiesAmount", 0);
      Cookies.set("multiplier", this.gameSettings.multiplier);
      Cookies.set("multiplierLevel", 1);
      Cookies.set("upgradeMultiplerPrice", 200);
      Cookies.set("idleCookiesPerSecond", 1);
      Cookies.set("idleCookiesPerSecondCost", 500);
      Cookies.set("idleCookiesPerSecondLevel", 1);
      this.setState({
        cookiesAmount: 0,
        multiplier: this.gameSettings.multiplier,
        multiplierLevel: 1,
        upgradeMultiplerPrice: 200,
        idleCookiesPerSecond: 1,
        idleCookiesPerSecondCost: 500,
        idleCookiesPerSecondLevel: 1,
      });
    }
  };

  render() {
    const {
      multiplier,
      multiplierLevel,
      upgradeMultiplerPrice,
      idleCookiesPerSecondCost,
      idleCookiesPerSecond,
      cookiesAmount,
      idleCookiesPerSecondLevel,
    } = this.state;

    const upgradeButtons = [
      {
        name: "upgradeMultiplierPrice",
        cost: upgradeMultiplerPrice,
        lvl: multiplierLevel,
        text: "Ulepsz mnożnik X2",
        click: this.UpgradeMultiplier,
      },
      {
        name: "upgradeIdleCookiesPerSecond",
        cost: idleCookiesPerSecondCost,
        text: " Ulepsz ciasteczka na sekunde X2",
        lvl: idleCookiesPerSecondLevel,
        click: this.UpgradeIdleCookiesPerSecond,
      },
    ];

    const { state, gameRestart } = this;

    return (
      <div className="app">
        <div className="cookieContainerLeft">
          <Cookie props={this.state} click={this.clickOnCookie} />
          <p className="cookieCounter">
            Ilość ciasteczek: {cookiesAmount} <br />
            Mnożnik: {multiplier} <br />
            Ciasteczka na sekunde: {idleCookiesPerSecond}
          </p>
        </div>

        <div className="cookieContainerRightShop">
          <CreateButtons upgradeButtons={upgradeButtons} state={state} />
        </div>

        <div className="restartButton" onClick={gameRestart}>
          RESTART
        </div>
      </div>
    );
  }
}

export default App;
